import React, { useEffect, useState } from "react";
import { Navigate } from "react-router-dom";
import hotkeys from "hotkeys-js";
import { useSelector } from "react-redux";
export default function ExitQrscreen(props) {
  let [checkval, setCheckVal] = useState("in");
  let settings = useSelector((data) => data.settings);
  let data = useSelector((data) => data.welcomeCheck);

  // console.log(props);
  useEffect(() => {
    let timer;
    if (window.location.hash === "#/qrScreen") {
      timer = setTimeout(() => {
        setCheckVal("out");
      }, 12000);
    } else {
      timer = setTimeout(() => {
        setCheckVal("out");
      }, 32000);
    }

    return () => {
      clearTimeout(timer);
    };
  }, [props.children]);
  if (data.count > 0 || localStorage.getItem("token")) {
    if (checkval === "in") {
      return props.children;
    } else {
      return <Navigate to={settings.visitTime ? "/visitTime" : "/companies"} />;
    }
  } else {
    return <Navigate to="/" />;
  }
}
