import React from "react";
import $ from "jquery";
import OwlCarousel from "react-owl-carousel";
import "owl.carousel/dist/assets/owl.carousel.css";
import "owl.carousel/dist/assets/owl.theme.default.css";
import slide1 from "../../assets/images/sliderImage/Slide1.PNG";
import slide2 from "../../assets/images/sliderImage/Slide2.PNG";
import slide3 from "../../assets/images/sliderImage/Slide3.PNG";
import slide4 from "../../assets/images/sliderImage/Slide4.PNG";
import slide5 from "../../assets/images/sliderImage/Slide5.PNG";
import slide6 from "../../assets/images/sliderImage/Slide6.PNG";
import slide7 from "../../assets/images/sliderImage/Slide7.PNG";
import slide8 from "../../assets/images/sliderImage/Slide8.PNG";
import slide9 from "../../assets/images/sliderImage/Slide9.PNG";
import slide10 from "../../assets/images/sliderImage/Slide10.PNG";
import s1 from "../../assets/images/sliderImage/slide4.jpg";
import s2 from "../../assets/images/sliderImage/slide5.jpg";
import s3 from "../../assets/images/sliderImage/353664159_697706755492762_8772312661363223880_n.jpg";
import s4 from "../../assets/images/sliderImage/cascades-carnival-event-poster.jpg";
import { Link } from "react-router-dom";
import { useEffect } from "react";
import { useSelector } from "react-redux";
export default function SliderEvents() {
  let arrOfImage = [
    slide1,
    slide2,
    slide3,
    slide4,
    slide5,
    slide6,
    slide7,
    slide8,
    slide9,
    slide10,
  ];
  let settings = useSelector((data) => data.settings);
  function Display() {
    document.getElementsByClassName("navbar")[0].classList.add("d-none");
    document.getElementsByClassName("container")[1].classList.add("slideCon");
    document.getElementById("root").classList.add("overflow-hidden");
  }
  function clear() {
    document.getElementsByClassName("navbar")[0].classList.remove("d-none");
    document
      .getElementsByClassName("container")[1]
      .classList.remove("slideCon");
    document.getElementById("root").classList.remove("overflow-hidden");
  }
  useEffect(() => {
    Display();
    return () => {
      clear();
    };
  }, []);
  return (
    <div className="layerPanel">
      <OwlCarousel
        className="owl-theme rtlClass"
        autoplay={true}
        items={1}
        dots={false}
        loop
      >
        {arrOfImage.map((image) => {
          return (
            <Link to={settings.visitTime ? "/visitTime" : "/companies"}>
              <img src={image} className="w-100" alt="" />
            </Link>
          );
        })}
      </OwlCarousel>
    </div>
  );
}
