// Import necessary React components, libraries, and assets
import React, { useEffect, useState } from "react";
import insertDocument from "../../assets/images/without BG.gif"; // Import image asset
import { Link, useNavigate } from "react-router-dom"; // Import routing components
import { useTranslation } from "react-i18next"; // Import i18next for internationalization
import { ToastContainer, toast } from "react-toastify"; // Import toast notifications
import { useSelector } from "react-redux"; // Import useSelector from react-redux for accessing state
import axiosInstance from "../../Intercpetor/AddTokenTohttp";

// Define the InsertDocumentInquere functional component
export default function InsertDocumentInquere() {
  let dataa = useSelector((data) => data.portSlice); // Access data from Redux state
  let host = useSelector((data) => data.portLoc);
  const { t, i18n } = useTranslation(); // Initialize the i18next translation hook
  let id = sessionStorage.getItem("doc"); // Get a value from sessionStorage
  let [OcrDataList, setOcrData] = useState(""); // Initialize state for OCR data
  let [Checkerorr, setErorr] = useState(false); // Initialize state for error checking

  let Navigate = useNavigate(); // Initialize the useNavigate hook for navigation

  useEffect(() => {
    notify(); // Display a toast notification
    toast.clearWaitingQueue(); // Clear any waiting toast notifications
    getDataOcr(); // Call the getDataOcr function on component mount
  }, []);

  function getName() {
    return t("Aftef30s"); // Get translated text for "Aftef30s"
  }

  // Function to display a toast notification
  const notify = () =>
    toast.warn(getName(), {
      position: "top-center",
      autoClose: 30000,
      hideProgressBar: false,
      closeOnClick: false,
      pauseOnHover: false,
      draggable: false,
      progress: undefined,
      theme: "colored",
      closeButton: false,
    });

  // Function to fetch OCR data from an API endpoint
  async function getDataOcr() {
    let data = await axiosInstance
      .get(`${host.port}api/Registrations/GetByDocumentId`, {
        params: {
          DocumentType: sessionStorage.getItem("docNo"), // Get a value from sessionStorage
        },
      })
      .then((res) => {
        if (window.location.hash === "#/insertCardinquere") {
          sessionStorage.setItem(
            "inquereResponse",
            `${JSON.stringify(res.data)}`
          );
          if (res.data.length === 1) {
            Navigate("/qrScreenInquere"); // Navigate to "/qrScreenInquere" route
            sessionStorage.setItem("index", 0); // Set a value in sessionStorage
          } else {
            Navigate("/VisitList"); // Navigate to "/VisitList" route
          }
        }
      })
      .catch((errorrr) => {
        if (window.location.hash === "#/insertCardinquere") {
          setErorr(true); // Set the error state to true
        }
      });
  }

  return (
    <div className="row h-100 justify-content-center">
      <ToastContainer limit={1} pauseOnFocusLoss={false} />{" "}
      {/* Display toast notifications container */}
      {Checkerorr ? (
        // Render a modal with an error message
        <div className="modal modal-dialog modal-dialog-centered">
          <div className="modal-content">
            <div className="modal-body">
              <h3 className="text-danger text-center">{t("insertError")}</h3>
            </div>
            <div className="modal-footer d-flex justify-content-center">
              <Link to="/visitTime" className="btn btn-primary">
                {t("registration")}
              </Link>
            </div>
          </div>
        </div>
      ) : (
        // Render content if there is no error
        <>
          <div className="col-md-8">
            <div className="row customShadow rounded-5 overflow-hidden">
              <div className="col-md-12 bg-info d-flex align-items-center justify-content-center">
                <h2 className="text-center text-white py-2">{t("EnterDoc")}</h2>
              </div>
              <div className="col-md-12 m-auto d-flex align-items-center justify-content-center overflow-hidden bg-white">
                <img
                  src={insertDocument}
                  alt="documentInsertImage"
                  width="600"
                  height="400"
                />
              </div>
            </div>
          </div>
        </>
      )}
    </div>
  );
}
