import React, { useEffect, useState } from "react";
import qr from "../../assets/images/image-3702.png";
import { Link, Navigate, useNavigate } from "react-router-dom";
import { useTranslation } from "react-i18next";
import greenMessage from "../../assets/images/think-before-you-print-logo-removebg-preview.png";
import ReactDOM from "react-dom";
import { QRCodeCanvas } from "qrcode.react";
import { ToastContainer, toast } from "react-toastify";
import { useSelector } from "react-redux";
import axiosInstance from "../../Intercpetor/AddTokenTohttp";
export default function QrScreenInquere() {
  // get App Settings From Redux
  let settings = useSelector((data) => data.settings);
  let host = useSelector((data) => data.portLoc);

  const { t, i18n } = useTranslation();
  let Navigate = useNavigate();
  let [loading, setLoading] = useState(false);
  let [OcrDataList, setOcrData] = useState([]);
  function getName() {
    return t("Aftef30s");
  }
  const notify = () =>
    toast.warn(getName(), {
      position: "top-center",
      autoClose: 30000,
      hideProgressBar: false,
      closeOnClick: false,
      pauseOnHover: false,
      draggable: false,
      progress: undefined,
      theme: "colored",
      closeButton: false,
    });

  let data = null;
  let qrImage = null;
  useEffect(() => {
    notify();
    toast.clearWaitingQueue();
    function qrData() {
      let can = document.getElementById("qrCanvas");
      qrImage = can.toDataURL();
      qrImage = qrImage.toString().split(",").slice(1).join("");
      sessionStorage.setItem("qrImage", qrImage);
      data = getOcrData();
      setOcrData(data);
    }
    qrData();
  }, []);
  function idSuceierty() {
    let id = JSON.parse(sessionStorage.getItem("ocr")).id;
    var idArr = id.split("");
    idArr.splice(4, id.length - 7, "******");
    return idArr.join("");
  }
  function getUserData() {
    let obj = {
      name: `${
        OcrDataList.documentFirstName + " " + OcrDataList.documentIdname
      }`,
      documentID: OcrDataList.documentIdno,
      companyId: sessionStorage.getItem("company"),
      registerationDate: OcrDataList.registrationDate,
      qrBase64: sessionStorage.getItem("qrImage"),
    };

    return JSON.stringify(obj);
  }
  function getName() {
    return t("Aftef30s");
  }
  async function Print() {
    setLoading(true);
    let userOBj = getUserData();
    console.log(userOBj);
    let dataa = await axiosInstance.post(`${host.port}api/print`, {
      headers: { "content-type": "application/json" },
      body: userOBj,
    });
    let daa = await dataa
      .json()

      .catch((errorrr) => {
        console.log(errorrr);
        setLoading(false);
      });
    setLoading(false);
  }
  function getOcrData() {
    return JSON.parse(sessionStorage.getItem("inquereResponse"))[
      JSON.parse(sessionStorage.getItem("index"))
    ];
  }

  return (
    <div className="row h-100 w-100 align-items-center justify-content-center p-3 ">
      <ToastContainer limit={1} />
      <div className="col-lg-5 col-md-7 justify-content-center align-items-center bg-white shadow rounded">
        <div className="qrCard p-4">
          <div className="d-flex gap-2">
            <h5>{t("Name")} :</h5>
            <span>{`${
              OcrDataList.documentFirstName + " " + OcrDataList.documentIdname
            }`}</span>
          </div>
          <div className="d-flex gap-2">
            <h5>{t("documentNumber")} :</h5>
            <span>{OcrDataList.documentIdno}</span>
          </div>
          <div className="d-flex gap-2">
            <h5>{t("Company")} :</h5>
            <span>{OcrDataList.companyName} </span>
          </div>

          {/* <div className="d-flex justify-content-center gap-2"> */}
          <div className="imgQr">
            <QRCodeCanvas
              value={OcrDataList.qr}
              size={100}
              marginSize={0}
              id="qrCanvas"
              renderAs="canvas"
              className="p-0"
            />
          </div>
          {/* </div> */}
          <div className="date">
            <span>{OcrDataList.registrationDate}</span>
          </div>

          <div className="d-flex w-100 gap-2 mt-4 justify-content-between">
            <Link
              to={settings.visitTime ? "/visitTime" : "/companies"}
              onClick={() => {
                Print();
              }}
              className="btn btn-primary w-50"
            >
              <span className="text-white">
                <i class="fa-solid fa-print"></i>
                {t("Print")}
              </span>
            </Link>
            <Link
              to={settings.visitTime ? "/visitTime" : "/companies"}
              className="btn btn-success w-50"
            >
              <span className="text-white">
                <i class="fa-solid fa-house"></i>
                {t("backHome")}
              </span>
            </Link>
          </div>
        </div>
        <div className="col-md-12 m-auto">
          <img
            src={greenMessage}
            alt="greenMessage"
            loading="lazy"
            height="100"
            className="w-100"
          />
        </div>
      </div>
    </div>
  );
}
