import React, { useEffect, useState } from "react";
import { Navigate } from "react-router-dom";
import { useSelector } from "react-redux";
export default function SliderConverter(props) {
  let data = useSelector((data) => data.welcomeCheck);

  let [checkval, setCheckVal] = useState("in");
  useEffect(() => {
    let timer = setTimeout(() => {
      setCheckVal("out");
    }, 32000);
    return () => {
      clearTimeout(timer);
    };
  }, [props.children]);
  if (data.count > 0 || localStorage.getItem("token")) {
    if (checkval === "in") {
      return props.children;
    } else {
      return <Navigate to="/slider" />;
    }
  } else {
    return <Navigate to="/" />;
  }
}
