// Import React and necessary components from React Router
import React from "react";
import { Outlet } from "react-router-dom";
import Navbar from "../Navbar/Navbar"; // Import the Navbar component
import { useEffect } from "react";

// Define the Layout functional component
export default function Layout() {
  useEffect(() => {
    localStorage.removeItem("token");
  }, []);
  return (
    <>
      <div className="bgImage"></div>
      <Navbar /> {/* Render the Navbar component */}
      <div className="d-flex w-100 h-100vh justify-content-center align-items-center py-3 position-relative zindexMain">
        {/* Create a container for content */}
        <div className="container h-100">
          <Outlet></Outlet>
          {/* Render the child components specified by the Router */}
        </div>
      </div>
    </>
  );
}
