import React, { useEffect, useState } from "react";
import $ from "jquery";
import WOw from "wow.js";
import logo from "../../assets/images/png-transparent-puma-logo-adidas-tamas-eyecare-adidas-cat-like-mammal-text-carnivoran-removebg-preview.png";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { ToastContainer, toast } from "react-toastify";
import { useSelector } from "react-redux";
import axiosInstance from "../../Intercpetor/AddTokenTohttp";
export default function Login() {
  // get App Settings From Redux
  let settings = useSelector((data) => data.settings);
  let host = useSelector((data) => data.portLoc);

  // set Loadder when async funcition To display loadder
  let [loading, setLoading] = useState(false);
  // Handle Error massage In Catch To Display Error massage To User
  let [errorr, setError] = useState(false);
  // Set Data Response From Api To Array
  let [ProductList, setProducts] = useState([]);
  // Varibale To Check If Commpent Dead Mount
  let check = "in";
  // Use State Is Hook TO Control Component Life Cycle
  useEffect(() => {
    // Check If Company Compenet Is A Main Compnent
    if (!settings.visitTime) {
      // Hide Back Button Because It's A Main Compnent
      document.getElementById("perv").classList.add("d-none");
      // Clear sessionStorage because It's A first page in user Roadmap
      sessionStorage.clear();
    }
    // clear All Toaster In Queue To Start A Empty Queue
    toast.clearWaitingQueue();
    // Init New Object From Wow Animation Libaray
    new WOw().init();
    // Call Get All Companies Funcition
    getComapny();
    // Compnent DeadMount
    return () => {
      // Display Back Button Again
      document.getElementById("perv").classList.remove("d-none");
      // Change Varibale Value To Out To know Compnent Is Dead
      check = "out";
    };
  }, [check]);
  // Set Language Funcition
  const { t, i18n } = useTranslation();
  // Async function getComapny How Get Data From Api
  async function getComapny() {
    setLoading(true);
    let data = await axiosInstance
      .get(`${host.port}api/Companies/GetAll`, {
        params: {
          deviceId: JSON.parse(localStorage.getItem("settings")).deviceId,
        },
      })
      .then((res) => {
        if (res.data.length === 0) {
          setError(true);
        } else {
          setProducts(res.data);
        }
        setLoading(false);
      })
      .catch((err) => {
        setError(true);
      });
  }
  // Function How Set Company Id and Company Name To sessionStorage
  function saveCompany(id, name, floor) {
    sessionStorage.setItem("company", `${id}`);
    sessionStorage.setItem("companyName", `${name}`);
    sessionStorage.setItem("floorName", `${floor}`);
  }
  return (
    <>
      {loading ? (
        <div className="loaderLayout">
          {errorr ? (
            <>
              <h1>{t("No Data")}</h1>
              <Link
                to={settings.visitTime ? "/visitTime" : "/"}
                className="btn btn-info"
              >
                {t("registration")}
              </Link>
            </>
          ) : (
            <div class="dot-spinner">
              <div class="dot-spinner__dot"></div>
              <div class="dot-spinner__dot"></div>
              <div class="dot-spinner__dot"></div>
              <div class="dot-spinner__dot"></div>
              <div class="dot-spinner__dot"></div>
              <div class="dot-spinner__dot"></div>
              <div class="dot-spinner__dot"></div>
              <div class="dot-spinner__dot"></div>
            </div>
          )}
        </div>
      ) : (
        <div className="row w-100 h-100">
          <ToastContainer limit={1} />
          {ProductList.map((product) => {
            return (
              <>
                <div
                  key={product.id}
                  className="col-md-4 col-lg-3 col-sm-6  product position-relative my-2"
                >
                  <Link
                    to={"/selectDepartment/" + product.id}
                    // to={
                    //   sessionStorage.getItem("status") === "schedule"
                    //     ? "/SelectTime"
                    //     : "/selectDoc"
                    // }
                    onClick={() => {
                      saveCompany(product.id, product.name, product.floorName);
                    }}
                  >
                    <div className="wow bounceInUp card border-0 rounded-5 overflow-hidden customShadow p-1 bg-white">
                      <div className="img-ComapnyCard w-100 d-flex align-items-center justify-content-center">
                        <img
                          src={"data:image/jpeg;base64," + product.logo}
                          className="rounded-5"
                          height="150px"
                          width="150px"
                          alt={product.name}
                        />
                      </div>
                      <div className="p-3 text-start">
                        <h5 className="fw-bold nameCompany">{product.name}</h5>

                        <div className="d-flex gap-2 my-2">
                          <i class="fa-solid fa-location-dot mainColor"></i>
                          <h6 className="text-muted m-0">
                            {product.floorName ? product.floorName : ""}
                          </h6>
                        </div>
                        <span class="badge text-bg-success">Open</span>
                      </div>
                    </div>
                  </Link>
                </div>
              </>
            );
          })}
        </div>
      )}
    </>
  );
}
