import React, { useEffect, useState } from "react";
import { Link, Navigate, useNavigate } from "react-router-dom";
import $, { event } from "jquery";
import hotkeys from "hotkeys-js";
import WOw from "wow.js";
import inquery from "../../assets/images/inquery.png";
import time from "../../assets/images/time_5246266.png";
import calender from "../../assets/images/calendar_2693507.png";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
export default function VisitTime() {
  const { t, i18n } = useTranslation();
  let settings = useSelector((data) => data.settings);
  function setTodayData() {
    let date = new Date();
    let dateMonth = date.toString().split(" ").slice(0, 4).join(" ");
    let Time = date.toString().split(" ").slice(4, 5).join(" ");
    let dataToday = {
      date: dateMonth,
      time: Time,
    };
    sessionStorage.setItem("today", JSON.stringify(dataToday));
  }
  useEffect(() => {
    document.getElementById("perv").classList.add("d-none");
    sessionStorage.clear();
    new WOw().init();
    return () => {
      document.getElementById("perv").classList.remove("d-none");
    };
  }, []);

  return (
    <div className="row h-100 w-100 g-3 justify-content-center">
      <div className="col-md-12 d-flex align-items-center justify-content-center flex-column">
        {/* <p className="">{t("pickOne")}</p> */}
      </div>
      {settings.Today ? (
        <div className="col-lg-4 d-flex align-items-center justify-content-center">
          <Link
            onClick={setTodayData}
            to="/companies"
            className="wow slideInLeft cardLang card rounded shadow px-5 py-3 d-flex align-items-center justify-content-center flex-column gap-2 border-0 rounded-5"
          >
            <div className="langImg p-2">
              <img src={time} alt="time" loading="lazy" />
            </div>
            <input type="hidden" id="inputHidden" />
            <h3>{t("Today")}</h3>
          </Link>
        </div>
      ) : (
        ""
      )}
      {settings.schedule ? (
        <div className="col-lg-4 d-flex align-items-center justify-content-center">
          <Link
            data-time="schedule"
            to="/companies"
            className=" wow bounceInUp cardLang card rounded shadow px-5 py-3 d-flex align-items-center justify-content-center flex-column gap-2 border-0 rounded-5"
            onClick={(e) => {
              sessionStorage.setItem("status", "schedule");
            }}
          >
            <div className="langImg p-3">
              <img src={calender} alt="calender" loading="lazy" />
            </div>
            <h3>{t("schedule")}</h3>
          </Link>
        </div>
      ) : (
        ""
      )}
      {settings.Inquire ? (
        <div className="col-lg-4 d-flex align-items-center justify-content-center ">
          <Link
            to="/selectDoc"
            className=" wow  slideInRight cardLang card rounded shadow px-5 py-3 d-flex align-items-center justify-content-center flex-column gap-2 border-0 rounded-5"
            onClick={(e) => {
              sessionStorage.setItem("status", "Inquire");
            }}
          >
            <div className="langImg p-2">
              <img src={inquery} alt="inquery" loading="lazy" />
            </div>
            <h3>{t("inquire")}</h3>
          </Link>
        </div>
      ) : (
        ""
      )}
    </div>
  );
}
