import React, { useEffect, useState } from "react";
import { Link, useNavigate, useParams } from "react-router-dom";
import $, { data } from "jquery";
import WOw from "wow.js";
import hrLogo from "../../assets/images/1436705.png";
import { useTranslation } from "react-i18next";
import { ToastContainer, toast } from "react-toastify";
import { useSelector } from "react-redux";
import axiosInstance from "../../Intercpetor/AddTokenTohttp";
export default function SelectDepratment() {
  let { id } = useParams();
  let [departmentList, setDepartment] = useState([]);
  let Navigate = useNavigate();
  // set Loadder when async funcition To display loadder
  let [loading, setLoading] = useState(false);
  // Handle Error massage In Catch To Display Error massage To User
  let [errorr, setError] = useState(false);
  let settings = useSelector((data) => data.settings);
  let host = useSelector((data) => data.portLoc);

  useState(() => {
    new WOw().init();
    getDepar();
  }, []);
  const { t, i18n } = useTranslation();
  function getName() {
    return t("Aftef30s");
  }
  const notify = () =>
    toast.warn(getName(), {
      position: "top-center",
      autoClose: 30000,
      hideProgressBar: false,
      closeOnClick: false,
      pauseOnHover: false,
      draggable: false,
      progress: undefined,
      theme: "colored",
      closeButton: false,
    });
  useEffect(() => {
    notify();
    toast.clearWaitingQueue();
  }, []);

  async function getDepar() {
    setLoading(true);
    let data = await axiosInstance
      .get(`${host.port}api/Departments/GetAll`, {
        params: { companyId: `${id}` },
      })
      .catch((errorrr) => {
        sessionStorage.getItem("status") === "schedule"
          ? Navigate("/selectTime")
          : Navigate("/selectDoc");
      });
    setLoading(false);
    if (data === undefined) {
      return;
    } else {
      setDepartment(data.data);
    }
  }
  function saveDepartment(id) {
    sessionStorage.setItem("department", `${id}`);
  }
  return (
    <>
      {loading ? (
        <div className="loaderLayout">
          {errorr ? (
            <>
              <h1>{t("No Data")}</h1>
              <Link
                to={settings.visitTime ? "/visitTime" : "/companies"}
                className="btn btn-info"
              >
                {t("registration")}
              </Link>
            </>
          ) : (
            <div class="dot-spinner">
              <div class="dot-spinner__dot"></div>
              <div class="dot-spinner__dot"></div>
              <div class="dot-spinner__dot"></div>
              <div class="dot-spinner__dot"></div>
              <div class="dot-spinner__dot"></div>
              <div class="dot-spinner__dot"></div>
              <div class="dot-spinner__dot"></div>
              <div class="dot-spinner__dot"></div>
            </div>
          )}
        </div>
      ) : (
        <div className="row w-100 h-100 g-3">
          <ToastContainer limit={1} />
          {departmentList.map((el) => {
            return (
              <div
                className="col-md-4 col-lg-3 col-sm-6  product position-relative"
                key={el.id}
              >
                <Link
                  to={`/selectEmpolyees/${el.id}`}
                  onClick={() => {
                    saveDepartment(el.id);
                  }}
                >
                  <div className="wow bounceInUp card  border-0 rounded-5 overflow-hidden customShadow">
                    <div className="cardCapition bg-white p-4 text-center">
                      <h3>{el.name}</h3>
                    </div>
                  </div>
                </Link>
              </div>
            );
          })}
        </div>
      )}
    </>
  );
}
