import React, { useEffect, useState } from "react";
import $ from "jquery";
import WOw from "wow.js";
import { Link, useNavigate, useParams } from "react-router-dom";
import axios from "axios";
import { useTranslation } from "react-i18next";
import { ToastContainer, toast } from "react-toastify";
export default function VisitScudedleList() {
  let [VisitScudedleList, setVisitDAta] = useState(null);
  let Navigate = useNavigate();

  useEffect(() => {
    setVisitDAta(JSON.parse(sessionStorage.getItem("inquereResponse")));

    toast.clearWaitingQueue();
    new WOw().init();
  }, []);
  const { t, i18n } = useTranslation();
  function getName() {
    return t("Aftef30s");
  }
  const notify = () =>
    toast.warn(getName(), {
      position: "top-center",
      autoClose: 30000,
      hideProgressBar: false,
      closeOnClick: false,
      pauseOnHover: false,
      draggable: false,
      progress: undefined,
      theme: "colored",
      closeButton: false,
    });

  function saveEmpolyee(id) {
    sessionStorage.setItem("index", id);
  }
  return (
    <div className="row w-100 h-100 g-3">
      <ToastContainer limit={1} />
      {VisitScudedleList !== null ? (
        VisitScudedleList.length === 0 ? (
          <div className="modal modal-dialog modal-dialog-centered">
            <div className="modal-content">
              <div className="modal-body">
                <h3 className="text-danger text-center">{t("noVisitFound")}</h3>
              </div>
              <div className="modal-footer d-flex justify-content-center">
                <Link to="/visitTime" className="btn btn-info">
                  {t("registration")}
                </Link>
              </div>
            </div>
          </div>
        ) : (
          VisitScudedleList.map((OcrDataList, i) => {
            return (
              <div
                className="col-md-6 col-lg-4  product position-relative"
                key={i}
              >
                <Link
                  to="/qrScreenInquere"
                  onClick={() => {
                    saveEmpolyee(i);
                  }}
                >
                  <div class="wow bounceInUp card p-2 rounded-5 shadow border-0 py-4">
                    <div className="row gap-3">
                      {/* <div className="col-md-12">
                      <div class="carddd__avatar m-auto overflow-hidden rounded-circle">
                        <img
                              src={"data:image/jpeg;base64," + el.photo}
                              alt="Avatar img"
                              className="w-100"
                            />
                      </div>
                    </div> */}
                      <div className="col-md-12 d-flex card-text">
                        <h5 class="mainColor">{t("Name")} :</h5>
                        <span>{`${
                          OcrDataList.documentFirstName +
                          " " +
                          OcrDataList.documentIdname
                        }`}</span>
                      </div>
                      <div className="col-md-12 d-flex card-text">
                        <h5 class="mainColor">{t("documentNumber")} :</h5>
                        <span>{OcrDataList.documentIdno}</span>
                      </div>
                      <div className="col-md-12 d-flex card-text">
                        <h5 class="mainColor">{t("Company")} :</h5>
                        <span>{OcrDataList.companyName} </span>
                      </div>
                      <div className="col-md-12 d-flex card-text">
                        <h5 class="mainColor">{t("Date")} :</h5>
                        <span>{OcrDataList.registrationDate}</span>
                      </div>
                    </div>
                  </div>
                </Link>
              </div>
            );
          })
        )
      ) : (
        <></>
      )}
    </div>
  );
}
