import React, { useEffect, useState } from "react";
import $ from "jquery";
import WOw from "wow.js";

import { Link, useNavigate, useParams } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { ToastContainer, toast } from "react-toastify";
import { useSelector } from "react-redux";
import axiosInstance from "../../Intercpetor/AddTokenTohttp";
export default function SelectEmpolyees() {
  let { id } = useParams();
  let [employeeList, setEmployees] = useState([]);
  let Navigate = useNavigate();
  // set Loadder when async funcition To display loadder
  let [loading, setLoading] = useState(false);
  // Handle Error massage In Catch To Display Error massage To User
  let [errorr, setError] = useState(false);
  let settings = useSelector((data) => data.settings);
  let host = useSelector((data) => data.portLoc);

  useState(() => {
    toast.clearWaitingQueue();
    new WOw().init();
    getEmpolyee();
  }, []);
  const { t, i18n } = useTranslation();
  function getName() {
    return t("Aftef30s");
  }
  const notify = () =>
    toast.warn(getName(), {
      position: "top-center",
      autoClose: 30000,
      hideProgressBar: false,
      closeOnClick: false,
      pauseOnHover: false,
      draggable: false,
      progress: undefined,
      theme: "colored",
      closeButton: false,
    });

  async function getEmpolyee() {
    setLoading(true);
    let data = await axiosInstance
      .get(`${host.port}api/Employees/GetAll`, {
        params: { departmentId: `${id}` },
      })
      .catch((errorrr) => {
        sessionStorage.getItem("status") === "schedule"
          ? Navigate("/selectTime")
          : Navigate("/selectDoc");
      });
    setLoading(false);
    if (data === undefined) {
      return;
    } else {
      notify();
      setEmployees(data.data);
    }
  }
  function saveEmpolyee(id) {
    sessionStorage.setItem("empolyee", `${id}`);
  }
  return (
    <>
      {loading ? (
        <div className="loaderLayout">
          {errorr ? (
            <>
              <h1>{t("No Data")}</h1>
              <Link
                to={settings.visitTime ? "/visitTime" : "/companies"}
                className="btn btn-info"
              >
                {t("registration")}
              </Link>
            </>
          ) : (
            <div class="dot-spinner">
              <div class="dot-spinner__dot"></div>
              <div class="dot-spinner__dot"></div>
              <div class="dot-spinner__dot"></div>
              <div class="dot-spinner__dot"></div>
              <div class="dot-spinner__dot"></div>
              <div class="dot-spinner__dot"></div>
              <div class="dot-spinner__dot"></div>
              <div class="dot-spinner__dot"></div>
            </div>
          )}
        </div>
      ) : (
        <div className="row w-100 h-100 g-3">
          <ToastContainer limit={1} />
          {employeeList.map((el) => {
            return (
              <div
                className="col-md-4 col-lg-3 col-sm-6  product position-relative"
                key={el.id}
              >
                <Link
                  to={
                    sessionStorage.getItem("status") === "schedule"
                      ? "/SelectTime"
                      : "/selectDoc"
                  }
                  onClick={() => {
                    saveEmpolyee(el.id);
                  }}
                >
                  <div class="wow bounceInUp card p-2 rounded-5 customShadow border-0 py-4">
                    <div className="row gap-3">
                      <div className="col-md-12">
                        <div class="carddd__avatar m-auto overflow-hidden rounded-circle">
                          <img
                            src={"data:image/jpeg;base64," + el.photo}
                            alt="Avatar img"
                            className="w-100"
                          />
                        </div>
                      </div>
                      <div className="col-md-12 card-text">
                        <h2 class="text-center text-dark">{el.name}</h2>
                        {/* <div class="text-muted text-center">Frontend</div> */}
                      </div>
                    </div>
                  </div>
                </Link>
              </div>
            );
          })}
        </div>
      )}
    </>
  );
}
