import React, { useEffect } from "react";
import { useTranslation } from "react-i18next";

import { Link, useParams } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";

export default function ErorrDocmentDisplay() {
  let { id } = useParams();
  const { t, i18n } = useTranslation();
  useEffect(() => {
    notify();
    toast.clearWaitingQueue();
    document.getElementById("perv").classList.add("d-none");
    return () => {
      document.getElementById("perv").classList.remove("d-none");
    };
  }, []);

  function getName() {
    return t("Aftef30s");
  }
  const notify = () =>
    toast.warn(getName(), {
      position: "bottom-center",
      autoClose: 30000,
      hideProgressBar: false,
      closeOnClick: false,
      pauseOnHover: false,
      draggable: false,
      progress: undefined,
      theme: "colored",
      closeButton: false,
    });
  return (
    <div className="row align-items-center justify-content-center h-100 w-100">
      <ToastContainer limit={1} />
      <div className="col-md-6">
        <div className="modal modal-dialog modal-dialog-centered">
          <div className="modal-content">
            <div className="modal-body">
              <h3 className="text-danger text-center">
                {JSON.parse(sessionStorage.getItem("erorrDocument"))}
              </h3>
            </div>
            <div className="modal-footer d-flex justify-content-center">
              <Link
                to={
                  id === "insertCardtype" ? "/insertCard" : "/insertCardinquere"
                }
                className="btn btn-primary"
              >
                {t("reEnter")}
              </Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
