import "./App.css";
import {
  createBrowserRouter,
  RouterProvider,
  Navigate,
  createHashRouter,
} from "react-router-dom";
import LAyout from "./component/Layout/LAyout";
import Login from "./component/Login/Login";
import SelectCompany from "./component/SelectCompany/SelectCompany";
import SelectDepratment from "./component/SelectDepratment/SelectDepratment";
import SelectEmpolyees from "./component/SelectEmpolyees/SelectEmpolyees";
import { Provider, useSelector } from "react-redux";
import { configreStore } from "./Redux/store";
import InserDocument from "./component/InserDocument/InserDocument";
import VistiorInformation from "./component/VistiorInformation/VistiorInformation";
import QrGenreate from "./component/QrGenreate/QrGenreate";
import VisitTime from "./component/VisitTime/VisitTime";
import CheckVisitTime from "./component/CheckVisitTime/CheckVisitTime";
import SelectTime from "./component/selectTime/SelectTime";
import Welcome from "./component/Welcome/Welcome";
import CheckWelcomeVisit from "./component/CheckWelcomeVisit/CheckWelcomeVisit";
import SelectDocument from "./component/SelectDocument/SelectDocument";
import QrScreen from "./component/QrScreen/QrScreen";
import ExitQrscreen from "./component/QrScreen/ExitQrscreen";
import UpdateOcrData from "./component/UpdateOcrData/UpdateOcrData";
import TestCamera from "./component/TestCamera/TestCamera";
import GardTime from "./component/selectTime/GardTime";
import SliderEvents from "./component/SliderEvents/SliderEvents";
import SliderConverter from "./component/VisitTime/SliderConverter";
import InquireOptions from "./component/InquireOptions/InquireOptions";
import InserDocumentinquere from "./component/InserDocument/InserDocumentinquere";
import QrScreenInquere from "./component/QrScreen/QrScreenInquere";
import VisitScudedleList from "./component/VisitScudedleList/VisitScudedleList";
import ErorrDocmentDisplay from "./component/InserDocument/ErorrDocmentDisplay";

function App() {
  document.addEventListener("contextmenu", (event) => event.preventDefault());
  function CheckMain() {
    if (settings.VisitTime === true) {
      return (
        <ExitQrscreen>
          <SelectCompany />
        </ExitQrscreen>
      );
    } else {
      return (
        <SliderConverter>
          <SelectCompany />
        </SliderConverter>
      );
    }
  }
  let counter = 0;
  let settings = useSelector((data) => data.settings);
  let routes = createHashRouter([
    {
      path: "/",
      element: <LAyout />,
      children: [
        {
          index: true,
          element: (
            <CheckWelcomeVisit>
              <Welcome />
            </CheckWelcomeVisit>
          ),
        },
        {
          path: "companies",
          element: (
            <SliderConverter>
              <SelectCompany />
            </SliderConverter>
          ),
        },
        {
          path: "selectDepartment/:id",
          element: (
            <ExitQrscreen>
              <SelectDepratment />
            </ExitQrscreen>
          ),
        },
        {
          path: "selectEmpolyees/:id",
          element: (
            <ExitQrscreen>
              <SelectEmpolyees />
            </ExitQrscreen>
          ),
        },
        {
          path: "selectDoc",
          element: (
            <ExitQrscreen>
              <SelectDocument />
            </ExitQrscreen>
          ),
        },
        {
          path: "UpdateOcrData",
          element: (
            <ExitQrscreen>
              <UpdateOcrData />
            </ExitQrscreen>
          ),
        },
        {
          path: "insertCard",
          element: (
            <ExitQrscreen>
              <InserDocument />
            </ExitQrscreen>
          ),
        },
        {
          path: "insertCardinquere",
          element: (
            <ExitQrscreen>
              <InserDocumentinquere />
            </ExitQrscreen>
          ),
        },
        {
          path: "vistiorInformation",
          element: (
            <ExitQrscreen>
              <VistiorInformation />
            </ExitQrscreen>
          ),
        },
        {
          path: "TestCamera",
          element: <TestCamera />,
        },
        {
          path: "visitTime",
          element: (
            <SliderConverter>
              <VisitTime />
            </SliderConverter>
          ),
        },
        {
          path: "selectInquire",
          element: (
            <ExitQrscreen>
              <InquireOptions />
            </ExitQrscreen>
          ),
        },
        {
          path: "slider",
          element: <SliderEvents />,
        },
        {
          path: "qrScreen",
          element: (
            <ExitQrscreen>
              <QrScreen />
            </ExitQrscreen>
          ),
        },
        {
          path: "qrScreenInquere",
          element: (
            <ExitQrscreen>
              <QrScreenInquere />
            </ExitQrscreen>
          ),
        },
        {
          path: "VisitList",
          element: (
            <ExitQrscreen>
              <VisitScudedleList />
            </ExitQrscreen>
          ),
        },
        {
          path: "selectTime",
          element: (
            <ExitQrscreen>
              <SelectTime />
            </ExitQrscreen>
          ),
        },
        {
          path: "erorrInsert/:id",
          element: (
            <ExitQrscreen>
              <ErorrDocmentDisplay />
            </ExitQrscreen>
          ),
        },
        // {
        //   path: "login",
        //   element: <Login />,
        // },
      ],
    },
  ]);
  return (
    <Provider store={configreStore}>
      <RouterProvider router={routes} />;
    </Provider>
  );
}

export default App;
