import { welcomeVisit } from "./CheckWelcome";
import { settings } from "./Settings";
import { portLoc } from "./port";

const { configureStore } = require("@reduxjs/toolkit");

export let configreStore = configureStore({
  reducer: {
    welcomeCheck: welcomeVisit,
    settings: settings,
    portLoc: portLoc,
  },
});
