import React, { useEffect, useState } from "react";
import insertDocument from "../../assets/images/driving_old.gif";
import insertDocument2 from "../../assets/images/without BG.gif";
import { Link, useNavigate } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { ToastContainer, toast } from "react-toastify";
import { data } from "jquery";
import { useSelector } from "react-redux";
import axiosInstance from "../../Intercpetor/AddTokenTohttp";
export default function InserDocument() {
  let settings = useSelector((data) => data.settings);
  let host = useSelector((data) => data.portLoc);

  const { t, i18n } = useTranslation();
  let id = sessionStorage.getItem("doc");
  let [OcrDataList, setOcrData] = useState("");
  let [erorrMessage, setErorrMessage] = useState("");
  let [Checkerorr, setErorr] = useState(false);

  let Navigate = useNavigate();
  useEffect(() => {
    notify();
    toast.clearWaitingQueue();
    getDataocr();
  }, []);
  function getName() {
    return t("Aftef30s");
  }
  const notify = () =>
    toast.warn(getName(), {
      position: "bottom-center",
      autoClose: 30000,
      hideProgressBar: false,
      closeOnClick: false,
      pauseOnHover: false,
      draggable: false,
      progress: undefined,
      theme: "colored",
      closeButton: false,
    });
  async function getDataocr() {
    let data = await axiosInstance
      .get(`${host.port}api/OCR/ReadDocument`, {
        params: {
          DocumentType: sessionStorage.getItem("docNo"),
          companyId: sessionStorage.getItem("company"),
          deviceId: JSON.parse(localStorage.getItem("settings")).deviceId,
          registrationDate: dateObj.orginalDate,
        },
      })
      .then((res) => {
        if (window.location.hash === "#/insertCard") {
          if (res.data.isSuccess == 1) {
            Navigate("/vistiorInformation");
            sessionStorage.setItem("ocr", `${JSON.stringify(res.data)}`);
          } else {
            setErorr(true);
            // Navigate("/insertCard");
            setErorrMessage(res.data.message);
          }
        } else {
          return;
        }
      })
      .catch((errorrr) => {
        if (errorrr.response.data.isSuccess === 0) {
          Navigate("/erorrInsert/insertCardtype");
          sessionStorage.setItem(
            "erorrDocument",
            JSON.stringify(errorrr.response.data.message)
          );
        } else if (errorrr.response.data.isSuccess === 2) {
          setErorr(true);
          setErorrMessage(errorrr.response.data.message);
        } else {
          setErorr(true);
          setErorrMessage(t("PleaseTryAgain"));
        }
      });
  }
  function checkDate() {
    if (sessionStorage.getItem("status") === "schedule") {
      let scheduleDAte = JSON.parse(sessionStorage.getItem("date"));
      let date = new Date(scheduleDAte.date + " " + scheduleDAte.time);
      let dateMonth = date.toString().split(" ").slice(0, 4).join(" ");
      let Time = date.toString().split(" ").slice(4, 5).join(" ");
      let monthFormat = date.toISOString();
      const offsetMinutes = date.getTimezoneOffset();
      date.setMinutes(date.getMinutes() - offsetMinutes);
      const formattedDate = date.toISOString().substring(0, 23);
      return {
        fullDate: monthFormat,
        orginalDate: formattedDate,
        date: dateMonth,
        time: Time,
      };
    } else {
      let date = new Date();
      let dateMonth = date.toString().split(" ").slice(0, 4).join(" ");
      let Time = date.toString().split(" ").slice(4, 5).join(" ");
      let monthFormat = date.toISOString();
      const offsetMinutes = date.getTimezoneOffset();
      date.setMinutes(date.getMinutes() - offsetMinutes);
      const formattedDate = date.toISOString().substring(0, 23);
      return {
        fullDate: monthFormat,
        orginalDate: formattedDate,
        date: dateMonth,
        time: Time,
      };
    }
  }
  let dateObj = checkDate();
  return (
    <div className="row h-100 justify-content-center ">
      <ToastContainer limit={1} pauseOnFocusLoss={false} />
      {Checkerorr ? (
        <div className="modal modal-dialog modal-dialog-centered">
          <div className="modal-content">
            <div className="modal-body">
              <h3 className="text-danger text-center" id="textAlert">
                {erorrMessage}
              </h3>
            </div>
            <div className="modal-footer d-flex justify-content-center">
              <Link
                to={settings.visitTime ? "/visitTime" : "/companies"}
                className="btn btn-primary"
              >
                {t("registration")}
              </Link>
            </div>
          </div>
        </div>
      ) : (
        // </div>
        <>
          <div className="col-md-8">
            <div className="row customShadow rounded-5 overflow-hidden">
              <div className="col-md-12 bg-info d-flex align-items-center justify-content-center">
                <h2 className="text-center text-white py-2">{t("EnterDoc")}</h2>
              </div>
              <div className="col-md-12 m-auto d-flex align-items-center justify-content-center overflow-hidden bg-white">
                <img
                  src={insertDocument2}
                  alt="documentInsertImage"
                  width="600"
                  height="400"
                />
              </div>
            </div>
          </div>
        </>
      )}
    </div>
  );
}
