import React, { useEffect, useState } from "react";
import logo from "../../assets/images/avms logo02-01.png";
import operaLogo from "../../assets/images/operaLogo.png";
import bg from "../../assets/images/website-welcome-message-examples.jpg";
import { Link, useNavigate } from "react-router-dom";
import { useTranslation } from "react-i18next";
import hotkeys from "hotkeys-js";
import { useDispatch, useSelector } from "react-redux";
import { changeSettings, welcomeVisit } from "../../Redux/CheckWelcome";
import axios, { Axios } from "axios";
import Login from "../Login/Login";
export default function Welcome() {
  let settings = useSelector((data) => data.settings);
  let host = useSelector((data) => data.portLoc);
  let [loading, setLoading] = useState(false);
  let [errorMsg, seterrMsg] = useState("");
  let [Erorr, setErorr] = useState(false);
  let [deviceType, setDeviceType] = useState("");
  const { t, i18n } = useTranslation();
  let Navigatee = useNavigate();
  hotkeys("f6", function (event, handler) {
    // Prevent the default refresh event under WINDOWS system
    event.preventDefault();
    start(event, handler);
  });
  async function getDeviceSettings() {
    setLoading(true);
    let data = await axios
      .get(`${host.port}api/Setting`)
      .then((res) => {
        setDeviceType(res.data.deviceType);
        seterrMsg(false);
        setLoading(false);
        setErorr(false);
        localStorage.setItem("settings", JSON.stringify(res.data));
        if (res.data.deviceType === "Kiosk") {
          localStorage.setItem("token", res.data.token);
        }
        Erorr ? console.log("ms--true") : console.log("ms--false");
      })
      .catch((error) => {
        setErorr(true);
        setLoading(false);
        document.getElementById("btnModalErorr").click();
        if (error?.response?.data) {
          seterrMsg(error.response.data);
        } else {
          seterrMsg(error.message);
        }
        return;
      });
  }
  function start(event, h) {
    Navigatee("/TestCamera");
  }
  let data = useSelector((data) => data.welcomeCheck);
  let disPAtch = useDispatch();
  function Display() {
    document.getElementsByClassName("navbar")[0].classList.add("d-none");
    document.getElementById("root").classList.add("overflow-hidden");
  }
  function clear() {
    document.getElementsByClassName("navbar")[0].classList.remove("d-none");
    document.getElementById("root").classList.remove("overflow-hidden");
  }
  useEffect(() => {
    Display();
    getDeviceSettings();
    return () => {
      clear();
    };
  }, []);
  function toggleFullScreen() {
    if (
      !document.fullscreenElement && // alternative standard method
      !document.mozFullScreenElement &&
      !document.webkitFullscreenElement &&
      !document.msFullscreenElement
    ) {
      // current working methods
      if (document.documentElement.requestFullscreen) {
        document.documentElement.requestFullscreen();
      } else if (document.documentElement.msRequestFullscreen) {
        document.documentElement.msRequestFullscreen();
      } else if (document.documentElement.mozRequestFullScreen) {
        document.documentElement.mozRequestFullScreen();
      } else if (document.documentElement.webkitRequestFullscreen) {
        document.documentElement.webkitRequestFullscreen(
          Element.ALLOW_KEYBOARD_INPUT
        );
      }
    }
  }

  return (
    <div className="w-100 h-100 testWelcome row m-auto rounded-5 overflow-hidden shadow">
      <button
        type="button"
        className="btn btn-primary d-none"
        data-bs-toggle="modal"
        data-bs-target="#exampleModal"
        id="btnModalErorr"
      ></button>

      <div
        className="modal fade"
        id="exampleModal"
        tabindex="-1"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-body">
              <h1 className="text-center" style={{ fontSize: "80px" }}>
                <i
                  className="fa-solid fa-x fa-xs"
                  style={{ color: "#f03d2d" }}
                ></i>
              </h1>
              <h3 id="textErorr" className="text-center">
                {errorMsg}
              </h3>
            </div>
            <div className="modal-footer">
              <button
                type="button"
                className="btn btn-secondary mx-auto"
                data-bs-dismiss="modal"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      </div>
      <div className="col-lg-6 d-flex flex-column bg-white align-items-center justify-content-around overflow-hidden p-4 position-relative">
        {Erorr ? (
          <div className="d-flex align-items-center justify-content-center flex-column">
            <h1 id="textErorr" className="text-center">
              {errorMsg}
            </h1>
            {loading ? (
              <button
                className="btn btn-outline-info rounded-5 px-5"
                type="button"
              >
                <span
                  className="spinner-border spinner-border-sm"
                  aria-hidden="true"
                ></span>
              </button>
            ) : (
              <button
                className="btn btn-outline-info my-3 rounded-5 px-5"
                onClick={() => {
                  getDeviceSettings();
                  // disPAtch(changeSettings());
                }}
              >
                Resend Request
              </button>
            )}
          </div>
        ) : deviceType === "Kiosk" ? (
          <>
            {/*Kiosk Role */}
            <div className="p-5">
              <img src={logo} alt="logo" className="w-100" />
            </div>
            {loading ? (
              <button
                className="btn btn-outline-info rounded-5 px-5"
                type="button"
              >
                <span
                  class="spinner-border spinner-border-sm"
                  aria-hidden="true"
                ></span>
              </button>
            ) : (
              <Link
                to={settings.visitTime ? "/visitTime" : "/companies"}
                className="btn btn-outline-info rounded-5 px-5"
                onClick={() => {
                  toggleFullScreen();
                  // disPAtch(changeSettings());
                }}
              >
                {t("Get Started")}
              </Link>
            )}
          </>
        ) : deviceType === "Terminal" ? (
          <Login />
        ) : (
          <></>
        )}
      </div>
      <div className="col-lg-6 p-0 h-100">
        <img src={bg} alt="" className="w-100 h-100" />
      </div>
    </div>
  );
}
