import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { Link } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";

export default function UpdateOcrData() {
  const { t, i18n } = useTranslation();
  let [checkInputData, setInputData] = useState({
    firstName: false,
    lastName: false,
    address: false,
    id: false,
  });

  let data = null;
  useEffect(() => {
    notify();
    toast.clearWaitingQueue();
    data = getOcrData();
    // setOcrData(data);
    inputValue(data);
  }, []);

  function getName() {
    return t("Aftef30s");
  }
  const notify = () =>
    toast.warn(getName(), {
      position: "top-center",
      autoClose: 30000,
      hideProgressBar: false,
      closeOnClick: false,
      pauseOnHover: false,
      draggable: false,
      progress: undefined,
      theme: "colored",
      closeButton: false,
    });

  function getOcrData() {
    return JSON.parse(sessionStorage.getItem("ocr"));
  }
  function inputValue(data) {
    let firstName = document.getElementById("firstName");
    let lastName = document.getElementById("lastName");
    let address = document.getElementById("address");
    let documentNumber = document.getElementById("documentNumber");
    firstName.value = data.firstName;
    lastName.value = data.lastName;
    address.value = data.address;
    documentNumber.value = data.id;
  }
  function updateData() {
    let dataObj = {
      firstName: document.getElementById("firstName").value,
      lastName: document.getElementById("lastName").value,
      enalishName: "",
      address: document.getElementById("address").value,
      id: document.getElementById("documentNumber").value,
      additionalInfo: "",
    };
    sessionStorage.setItem("ocr", JSON.stringify(dataObj));
  }

  return (
    <div className="row h-100 w-100 justify-content-center align-items-center">
      <ToastContainer limit={1} />
      <div className="col-md-10 m-auto">
        <div className="row shadow p-5 rounded bg-white">
          <div className="col-md-12">
            <div class="form-floating mb-3">
              <input
                type="text"
                class="form-control"
                id="firstName"
                onKeyUp={() => {
                  setInputData({
                    firstName: document.getElementById("firstName").value,
                    lastName: document.getElementById("lastName").value,
                    address: document.getElementById("address").value,
                    id: document.getElementById("documentNumber").value,
                  });
                }}
              />
              <label for="firstName">{t("firstName")}</label>
            </div>
          </div>
          <div className="col-md-12">
            <div class="form-floating mb-3">
              <input
                type="text"
                class="form-control"
                id="lastName"
                onKeyUp={() => {
                  setInputData({
                    firstName: document.getElementById("firstName").value,
                    lastName: document.getElementById("lastName").value,
                    address: document.getElementById("address").value,
                    id: document.getElementById("documentNumber").value,
                  });
                }}
              />
              <label for="lastName">{t("lastName")}</label>
            </div>
          </div>
          <div className="col-md-12">
            <div class="form-floating mb-3">
              <input
                type="text"
                class="form-control"
                id="address"
                onKeyUp={() => {
                  setInputData({
                    firstName: document.getElementById("firstName").value,
                    lastName: document.getElementById("lastName").value,
                    address: document.getElementById("address").value,
                    id: document.getElementById("documentNumber").value,
                  });
                }}
              />
              <label for="address">{t("address")}</label>
            </div>
          </div>
          <div className="col-md-12">
            <div class="form-floating mb-3">
              <input
                type="text"
                class="form-control"
                id="documentNumber"
                onKeyUp={() => {
                  setInputData({
                    firstName: document.getElementById("firstName").value,
                    lastName: document.getElementById("lastName").value,
                    address: document.getElementById("address").value,
                    id: document.getElementById("documentNumber").value,
                  });
                }}
              />
              <label for="documentNumber">{t("documentNumber")}</label>
            </div>
          </div>
          <div className="col-md-3 m-auto">
            <Link
              to="/vistiorInformation"
              className={
                checkInputData.firstName &&
                checkInputData.lastName &&
                checkInputData.address &&
                checkInputData.id
                  ? "btn btn-info "
                  : "btn btn-info  disabled"
              }
              onClick={updateData}
            >
              {t("update")}
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
}
