// Import necessary React components, libraries, and assets
import React, { useEffect, useState } from "react";
import { Link, Navigate, useNavigate } from "react-router-dom";
import $, { event } from "jquery"; // Import jQuery (Note: Consider using React's state management instead)
import hotkeys from "hotkeys-js";
import WOw from "wow.js"; // Import the Wow.js library for animations
import inquereQR from "../../assets/images/SearchQr.png"; // Import image assets
import inquereCard from "../../assets/images/serchCard.png";
import { useTranslation } from "react-i18next"; // Import i18next for internationalization
import { ToastContainer, toast } from "react-toastify"; // Import toast notifications

// Define the InquireOptions functional component
export default function InquireOptions() {
  // Initialize the i18next translation hook
  const { t, i18n } = useTranslation();

  // Function to get translated text for a specific duration
  function getName() {
    return t("Aftef30s"); // Get translated text for "Aftef30s"
  }

  // Function to display a toast notification
  const notify = () =>
    toast.warn(getName(), {
      position: "top-center",
      autoClose: 30000,
      hideProgressBar: false,
      closeOnClick: false,
      pauseOnHover: false,
      draggable: false,
      progress: undefined,
      theme: "colored",
      closeButton: false,
    });

  // Initialize state and side effects using the useState and useEffect hooks
  useState(() => {
    new WOw().init(); // Initialize Wow.js for animations
    notify(); // Display the toast notification
    toast.clearWaitingQueue(); // Clear any waiting toast notifications
  }, []);

  // Render the component
  return (
    <div className="row h-100 w-100 g-3">
      <ToastContainer limit={1} /> {/* Display toast notifications container */}

      {/* Render a column with text */}
      <div className="col-md-12 d-flex align-items-center justify-content-center flex-column">
        <p className="text-white">{t("pickOne")}</p>
      </div>

      {/* Render a column with a link to inquire about documents */}
      <div className="col-md-6 d-flex align-items-center justify-content-center">
        <Link
          to="/selectDoc"
          className=" wow bounceInLeft cardLang card rounded shadow p-5 d-flex align-items-center justify-content-center flex-column gap-2 border-0 rounded-5"
        >
          <div className="langImg p-2">
            <img src={inquereCard} alt="inquereCard" loading="lazy" />
          </div>
          <input type="hidden" id="inputHidden" />
          <h3>{t("inquire about document")}</h3>
        </Link>
      </div>

      {/* Render a column with a link to inquire about QR codes */}
      <div className="col-md-6 d-flex align-items-center justify-content-center ">
        <Link
          to="/selectDoc"
          className=" wow bounceInLeft cardLang card rounded shadow p-5 d-flex align-items-center justify-content-center flex-column gap-2 border-0 rounded-5"
        >
          <div className="langImg p-2">
            <img src={inquereQR} alt="inquereQR" loading="lazy" />
          </div>
          <h3>{t("inquire about qr")}</h3>
        </Link>
      </div>
    </div>
  );
}
