import React, { useEffect, useState } from "react";
import document from "../../assets/images/images.jpg";
import { Link, useNavigate } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { ToastContainer, toast } from "react-toastify";
import { useSelector } from "react-redux";
import axiosInstance from "../../Intercpetor/AddTokenTohttp";
export default function VistiorInformation() {
  const { t, i18n } = useTranslation();
  let Navigate = useNavigate();
  let [loading, setLoading] = useState(false);
  let [eroeer, setErorr] = useState(false);
  let [erorrMsg, setErorrMsg] = useState(false);
  let [OcrDataList, setOcrData] = useState([]);
  let settings = useSelector((data) => data.settings);
  let host = useSelector((data) => data.portLoc);
  useEffect(() => {
    notify();
    toast.clearWaitingQueue();
    if (!settings.visitorInformation) {
      getOcrData();
    } else {
      setOcrData(JSON.parse(sessionStorage.getItem("ocr")));
    }
  }, []);
  function getUserData(OcrDataList) {
    let obj = {
      name: `${OcrDataList.firstName}`,
      zoneId: JSON.parse(localStorage.getItem("settings")).zoneId,
      buildingId: JSON.parse(localStorage.getItem("settings")).buildingId,
      gateId: JSON.parse(localStorage.getItem("settings")).gateId,
      companyId: sessionStorage.getItem("company"),
      deparmentId: sessionStorage.getItem("department"),
      employeeId: sessionStorage.getItem("empolyee"),
      deviceId: JSON.parse(localStorage.getItem("settings")).deviceId,
      documentIdname: `${OcrDataList.lastName}`,
      documentCameraShot: sessionStorage
        .getItem("picture")
        .split(",")
        .slice(1)
        .join(),
      documentImageId: OcrDataList.documentImage,
      documentIdenglishName: OcrDataList.enalishName,
      documentIdno: OcrDataList.id,
      documentIdaddress: OcrDataList.address,
      registrationDate: dateObj.orginalDate,
      registrationTime: dateObj.time,
      description: null,
      notes: null,
      Qr: `${JSON.parse(sessionStorage.getItem("ocr")).cardNumber}`,
      documentFirstName: `${OcrDataList.firstName}`,
      documentNumber: null,
    };
    sendUserData(JSON.stringify(obj));
  }
  function getName() {
    return t("Aftef30s");
  }
  async function sendUserData(userOBj) {
    setLoading(true);
    var myHeaders = new Headers();
    myHeaders.append("Content-Type", "application/json");
    myHeaders.append(
      "Authorization",
      `Bearer ${localStorage.getItem("token")}`
    );

    var requestOptions = {
      method: "POST",
      headers: myHeaders,
      body: userOBj,
      redirect: "follow",
    };

    fetch(`${host.port}api/Registrations`, requestOptions)
      .then((res) => {
        setLoading(false);
        Navigate("/qrScreen");
        sessionStorage.setItem("finalData", `${JSON.stringify(res.data)}`);
      })
      .catch((errorrr) => {
        setLoading(false);
        setErorr(true);
        setErorrMsg(errorrr);
      });
  }
  const notify = () =>
    toast.warn(getName(), {
      position: "top-center",
      autoClose: 30000,
      hideProgressBar: false,
      closeOnClick: false,
      pauseOnHover: false,
      draggable: false,
      progress: undefined,
      theme: "colored",
      closeButton: false,
    });

  function getOcrData() {
    getUserData(JSON.parse(sessionStorage.getItem("ocr")));
  }
  function checkDate() {
    if (sessionStorage.getItem("status") === "schedule") {
      let scheduleDAte = JSON.parse(sessionStorage.getItem("date"));
      let date = new Date(scheduleDAte.date + " " + scheduleDAte.time);
      let dateMonth = date.toString().split(" ").slice(0, 4).join(" ");
      let Time = date.toString().split(" ").slice(4, 5).join(" ");
      let monthFormat = date.toISOString();
      const offsetMinutes = date.getTimezoneOffset();
      date.setMinutes(date.getMinutes() - offsetMinutes);
      const formattedDate = date.toISOString().substring(0, 23);
      return {
        fullDate: monthFormat,
        orginalDate: formattedDate,
        date: dateMonth,
        time: Time,
      };
    } else {
      let date = new Date();
      let dateMonth = date.toString().split(" ").slice(0, 4).join(" ");
      let Time = date.toString().split(" ").slice(4, 5).join(" ");
      let monthFormat = date.toISOString();
      const offsetMinutes = date.getTimezoneOffset();
      date.setMinutes(date.getMinutes() - offsetMinutes);
      const formattedDate = date.toISOString().substring(0, 23);
      return {
        fullDate: monthFormat,
        orginalDate: formattedDate,
        date: dateMonth,
        time: Time,
      };
    }
  }
  let dateObj = checkDate();
  return (
    <div className="row visitorInfor h-100 justify-content-center align-items-center">
      <ToastContainer limit={1} />
      {eroeer ? (
        <div className="modal modal-dialog modal-dialog-centered">
          <div className="modal-content">
            <div className="modal-body">
              <h3 className="text-danger text-center" id="textAlert">
                reveiw your Data
              </h3>
            </div>
            <div className="modal-footer d-flex justify-content-center">
              <Link
                to={settings.visitTime ? "/visitTime" : "/companies"}
                className="btn btn-primary"
              >
                {t("registration")}
              </Link>
            </div>
          </div>
        </div>
      ) : settings.visitorInformation ? (
        <>
          <div className="col-md-10 m-auto">
            <div className="row gap-4 justify-content-center bg-white rounded-5 p-3">
              <div className="col-lg-7 col-md-9">
                <h2 className="mb-5 text-center">{t("VisitorInfor")}</h2>
              </div>
              <div className="col-lg-7 col-md-9 d-flex justify-content-between">
                <h5>
                  <i class="fa-solid fa-user mx-2"></i>
                  <span>{t("fullName")}</span>
                </h5>
                <span>
                  {`${OcrDataList.firstName}` + " " + `${OcrDataList.lastName}`}
                </span>
              </div>
              <div className="col-lg-7 col-md-9 d-flex justify-content-between">
                <h5>
                  <i class="fa-solid fa-map-location-dot mx-2"></i>
                  <span>{t("address")}</span>
                </h5>
                <span>{OcrDataList.address}</span>
              </div>
              <div className="col-lg-7 col-md-9 d-flex justify-content-between">
                <h5>
                  <i class="fa-solid fa-id-card mx-2"></i>
                  <span>{t("documentType")}</span>
                </h5>
                <span>{sessionStorage.getItem("doc")}</span>
              </div>
              <div className="col-lg-7 col-md-9 d-flex justify-content-between">
                <h5>
                  <i class="fa-solid fa-xmarks-lines mx-2"></i>
                  <span>{t("documentNumber")}</span>
                </h5>
                <span>{OcrDataList.id}</span>
              </div>
              <div className="col-lg-7 col-md-9 d-flex justify-content-between">
                <h5>
                  <i class="fa-solid fa-calendar-days mx-2"></i>
                  <span>{t("date")}</span>
                </h5>
                <span>{dateObj.date}</span>
              </div>
              <div className="col-lg-7 col-md-9 d-flex justify-content-between">
                <h5>
                  <i class="fa-solid fa-clock mx-2"></i>
                  <span>{t("time")}</span>
                </h5>
                <span>{dateObj.time}</span>
              </div>
              <div className="col-lg-7 col-md-9">
                <div className="row justify-content-between">
                  <div className="col-md-4">
                    {settings.editData ? (
                      <Link
                        to="/UpdateOcrData"
                        className="btn btn-danger w-100 mb-2"
                      >
                        {t("edit")}
                      </Link>
                    ) : (
                      ""
                    )}
                  </div>
                  <div className="col-md-4">
                    {loading ? (
                      <button type="button" className="btn btn-success">
                        <i className="fa-solid fa-spinner fa-spin text-white"></i>
                      </button>
                    ) : (
                      <button
                        className="btn btn-info w-100"
                        onClick={() => {
                          getOcrData();
                        }}
                      >
                        {t("save")}
                      </button>
                    )}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </>
      ) : (
        <></>
      )}
    </div>
  );
}
