import React, { useEffect, useState } from "react";
import qr from "../../assets/images/image-3702.png";
import { Link, Navigate, useNavigate } from "react-router-dom";
import { useTranslation } from "react-i18next";
import greenMessage from "../../assets/images/think-before-you-print-logo-removebg-preview.png";
import ReactDOM from "react-dom";
import { QRCodeCanvas } from "qrcode.react";
import { ToastContainer, toast } from "react-toastify";
import { useSelector } from "react-redux";
import axiosInstance from "../../Intercpetor/AddTokenTohttp";
export default function QrScreen() {
  // get App Settings From Redux
  let settings = useSelector((data) => data.settings);
  let host = useSelector((data) => data.portLoc);
  // Set Language Funcition
  const { t, i18n } = useTranslation();
  // Navigate Function ToSwitch To Anther Page
  let Navigate = useNavigate();
  // set Loadder when async funcition To display loadder
  let [loading, setLoading] = useState(false);
  // let [erorr, setErorr] = useState(false);
  // Set Data To Display It To User And To Sent It To Print Api Funcition
  let [OcrDataList, setOcrData] = useState([]);
  // Function How Get Text To Diplay It in Toaster
  function getName() {
    return t("Aftef10s");
  }

  function Display() {
    document.getElementsByClassName("navbar")[0].classList.add("d-none");
    document.getElementById("root").classList.add("overflow-hidden");
  }
  function clear() {
    document.getElementsByClassName("navbar")[0].classList.remove("d-none");
    document.getElementById("root").classList.remove("overflow-hidden");
  }
  // Function Who Initlization Toaster
  const notify = () =>
    toast.warn(getName(), {
      position: "top-center",
      autoClose: 10000,
      hideProgressBar: false,
      closeOnClick: false,
      pauseOnHover: false,
      draggable: false,
      progress: undefined,
      theme: "colored",
      closeButton: false,
    });
  const notifyErorr = () =>
    toast.error("Print failed", {
      position: "right-center",
      autoClose: 10000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: false,
      draggable: false,
      progress: undefined,
      theme: "light",
      closeButton: false,
    });
  // Varibale Who Set Data Into
  let data = null;
  // Varibale Who Set Qr Image Base64 Into
  let qrImage = null;
  useEffect(() => {
    Display();
    // clear All Toaster In Queue To Start A Empty Queue
    // Call Toster Funcition
    notify();
    // Function who Get Qr Image
    function qrData() {
      let can = document.getElementById("qrCanvas");
      qrImage = can.toDataURL();
      qrImage = qrImage.toString().split(",").slice(1).join("");
      sessionStorage.setItem("qrImage", qrImage);
      data = getOcrData();
      setOcrData(data);
    }
    // Call QrData Function
    qrData();
    Print();
    return () => {
      clear();
    };
  }, []);
  function idSuceierty() {
    let id = JSON.parse(sessionStorage.getItem("ocr")).id;
    var idArr = id.split("");
    idArr.splice(4, id.length - 7, "******");
    return idArr.join("");
  }
  // set Object Of Data and Save It To SessionStorage
  function getUserData() {
    let obj = {
      printerName: JSON.parse(localStorage.getItem("settings")).printerName,
      name: `${JSON.parse(sessionStorage.getItem("ocr")).firstName} ${
        JSON.parse(sessionStorage.getItem("ocr")).lastName
      }`,
      // documentID: JSON.parse(sessionStorage.getItem("ocr")).id,
      documentID: idSuceierty(),
      companyId: sessionStorage.getItem("company"),
      companyName: sessionStorage.getItem("companyName"),
      floorName: sessionStorage.getItem("floorName"),
      registerationDate: dateObj.orginalDate,
      qrBase64: sessionStorage.getItem("qrImage"),
    };
    return JSON.stringify(obj);
  }
  // Function Print Send data to Api
  async function Print() {
    setLoading(true);
    let userOBj = getUserData();
    var myHeaders = new Headers();
    myHeaders.append("Content-Type", "application/json");
    myHeaders.append(
      "Authorization",
      `Bearer ${localStorage.getItem("token")}`
    );

    var requestOptions = {
      method: "POST",
      headers: myHeaders,
      body: userOBj,
      redirect: "follow",
    };

    fetch(`${host.port}api/print`, requestOptions)
      .then((res) => {
        setLoading(false);
      })
      .catch((errorrr) => {
        console.log(errorrr);
        setLoading(false);
      });
  }
  // Get data User From sessionStorage
  function getOcrData() {
    return JSON.parse(sessionStorage.getItem("ocr"));
  }
  // Function Check If Visit Today or schedule And Set Time Format
  function checkDate() {
    if (sessionStorage.getItem("status") === "schedule") {
      let scheduleDAte = JSON.parse(sessionStorage.getItem("date"));
      let date = new Date(scheduleDAte.date + " " + scheduleDAte.time);
      let dateMonth = date.toString().split(" ").slice(0, 4).join(" ");
      let Time = date.toString().split(" ").slice(4, 5).join(" ");
      let monthFormat = date.toISOString();
      const offsetMinutes = date.getTimezoneOffset();
      date.setMinutes(date.getMinutes() - offsetMinutes);
      const formattedDate = date.toISOString().substring(0, 23);
      return {
        fullDate: monthFormat,
        orginalDate: formattedDate,
        date: dateMonth,
        time: Time,
      };
    } else {
      let date = new Date();
      let dateMonth = date.toString().split(" ").slice(0, 4).join(" ");
      let Time = date.toString().split(" ").slice(4, 5).join(" ");
      let monthFormat = date.toISOString();
      const offsetMinutes = date.getTimezoneOffset();
      date.setMinutes(date.getMinutes() - offsetMinutes);
      const formattedDate = date.toISOString().substring(0, 23);
      return {
        fullDate: monthFormat,
        orginalDate: formattedDate,
        date: dateMonth,
        time: Time,
      };
    }
  }
  // Varibale Who Storage A Date time
  let dateObj = checkDate();
  return (
    <div className="row h-100 w-100 align-items-center justify-content-center p-3">
      <ToastContainer pauseOnFocusLoss={false} limit={1} />
      <div className="col-lg-5 col-md-7 justify-content-center align-items-center">
        <div className="qrCard shadow p-4 rounded bg-white">
          {/* <div className="d-flex gap-2">
            <h5>{t("Name")} :</h5>
            <span>
              {OcrDataList.firstName || OcrDataList.lastName
                ? `${OcrDataList.firstName + " " + OcrDataList.lastName}`
                : OcrDataList.enalishName}
            </span>
          </div> */}
          <div className="d-flex gap-2">
            <h5>{t("documentNumber")} :</h5>
            <span>{idSuceierty()}</span>
          </div>
          <div className="d-flex gap-2">
            <h5>{t("Company")} :</h5>
            <span>{sessionStorage.getItem("companyName")} </span>
          </div>
          {sessionStorage.getItem("floorName") === "null" ||
          sessionStorage.getItem("floorName") === "undefined" ? (
            ""
          ) : (
            <div className="d-flex gap-2">
              <h5>{t("floor")} :</h5>
              <span>{sessionStorage.getItem("floorName")} </span>
            </div>
          )}
          {/* <div className="d-flex justify-content-center gap-2"> */}
          <div className="imgQr">
            <QRCodeCanvas
              value={JSON.parse(sessionStorage.getItem("ocr")).cardNumber}
              size={100}
              marginSize={0}
              id="qrCanvas"
              renderAs="canvas"
              className="p-0"
            />
          </div>
          {/* </div> */}
          <div className="date">
            <span>
              {dateObj.orginalDate.toString().split(" ").slice(0, 5).join(" ")}
            </span>
          </div>

          <div className="d-flex w-100 gap-2 mt-4 justify-content-between">
            {/* <Link
              to={settings.visitTime ? "/visitTime" : "/companies"}
              onClick={() => {
                Print();
              }}
              className="btn btn-primary w-50"
            >
              <span className="text-white">
                <i class="fa-solid fa-print mx-2"></i>
                {t("Print")}
              </span>
            </Link> */}
            <Link
              to={settings.visitTime ? "/visitTime" : "/companies"}
              className="btn btn-info w-50"
            >
              <span className="text-white">
                <i class="fa-solid fa-house mx-2"></i>
                {t("backHome")}
              </span>
            </Link>
          </div>
        </div>
        <div className="col-md-12 m-auto">
          <img
            src={greenMessage}
            alt="greenMessage"
            loading="lazy"
            height="100"
            className="w-100"
          />
        </div>
      </div>
    </div>
  );
}
