import { createSlice } from "@reduxjs/toolkit";

let settingSlice = createSlice({
  name: "settings",
  initialState: {
    visitTime: false,
    Today: false,
    schedule: false,
    Inquire: false,
    visitorInformation: false,
    editData: false,
    selectDoc: true,
    Welcome: true,
    insertDoc: true,
    slider: true,
    passport: true,
    nationalId: true,
    newDrive: true,
    oldDrive: true,
  },
});
export let settings = settingSlice.reducer;
