import React, { useEffect, useState } from "react";
import NationalId from "../../assets/images/nationalid.png";
import passPort from "../../assets/images/passport.png";
import oldDriven from "../../assets/images/olddl.png";
import newDriven from "../../assets/images/newdl.png";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { ToastContainer, toast } from "react-toastify";
import { useSelector } from "react-redux";
export default function SelectDocument() {
  // get App Settings From Redux
  let settings = useSelector((data) => data.settings);

  const { t, i18n } = useTranslation();
  let [Checkerorr, setErorr] = useState(false);
  function getName() {
    return t("Aftef30s");
  }
  const notify = () =>
    toast.warn(getName(), {
      position: "bottom-center",
      autoClose: 30000,
      hideProgressBar: false,
      closeOnClick: false,
      pauseOnHover: false,
      draggable: false,
      progress: undefined,
      theme: "colored",
      closeButton: false,
    });
  let video = document.getElementById("#video");
  let canvas = document.querySelector("#canvas");

  async function startCamera() {
    let stream = await navigator.mediaDevices
      .getUserMedia({
        video: {
          deviceId: localStorage.getItem("cameraId"),
        },
        audio: false,
      })
      .catch((err) => {
        console.log(err);
      });
    // stream.getVideoTracks()[0].label = "HP HD Camera (04f2:b51c)";
    video.srcObject = stream;
    setTimeout(() => {
      try {
        video.play();
        clickImage(stream);
      } catch (error) {
        console.log(error);
      }
    }, 100);
  }

  function clickImage(st) {
    const video = document.getElementById("video");
    let canvas = document.querySelector("#canvas");
    const context = canvas.getContext("2d");

    canvas.width = 150;
    canvas.height = 150;
    context.drawImage(video, 0, 0, 150, 150);
    const data = canvas.toDataURL("image/png");
    sessionStorage.setItem("picture", data);

    closeCamera(st);
  }

  function closeCamera(stream) {
    video.classList.add("d-none");
    canvas.classList.add("d-none");
    setErorr(true);
    let tracks = stream.getTracks();
    tracks[0].stop();
  }
  useEffect(() => {
    video = document.querySelector("#video");
    canvas = document.querySelector("#canvas");
    startCamera();
    notify();
    toast.clearWaitingQueue();
  }, []);
  return (
    <div className="row justify-content-center h-100">
      <video id="video" width="320" height="240" autoplay className=""></video>
      <canvas id="canvas" className="" width="320" height="240"></canvas>
      <ToastContainer pauseOnFocusLoss={false} limit={1} />
      {Checkerorr ? (
        <div className="col-lg-10">
          <div className="row  justify-content-center">
            {settings.nationalId ? (
              <div className="col-lg-5 col-md-6  p-2">
                <Link
                  to={
                    sessionStorage.getItem("status") === "Inquire"
                      ? "/insertCardinquere"
                      : "/insertCard"
                  }
                  onClick={(e) => {
                    sessionStorage.setItem("doc", "National Id");
                    sessionStorage.setItem("docNo", "1");
                  }}
                >
                  <div className="rounded-5 customShadow  overflow-hidden">
                    <img
                      src={NationalId}
                      alt="NationalId"
                      className="w-100 imgDoc"
                    />
                    <div className="cardCapition p-3 text-center bg-white">
                      <h3>{t("National Id")}</h3>
                    </div>
                  </div>
                </Link>
              </div>
            ) : (
              ""
            )}
            {settings.newDrive ? (
              <div className="col-lg-5  col-md-6  p-2">
                <Link
                  className="rounded-5 customShadow  overflow-hidden"
                  to="/insertCard"
                  onClick={(e) => {
                    sessionStorage.setItem("doc", "New Driving Licence");
                    sessionStorage.setItem("docNo", "3");
                  }}
                >
                  <div className="rounded-5 customShadow  overflow-hidden">
                    <img
                      src={newDriven}
                      alt="newDriven"
                      className="w-100 imgDoc"
                    />
                    <div className="cardCapition p-3 text-center bg-white">
                      <h3>{t("New Driving Licence")}</h3>
                    </div>
                  </div>
                </Link>
              </div>
            ) : (
              ""
            )}
            {settings.passport ? (
              <div className="col-lg-5 col-md-6 p-2">
                <Link
                  to={
                    sessionStorage.getItem("status") === "Inquire"
                      ? "/insertCardinquere"
                      : "/insertCard"
                  }
                  onClick={(e) => {
                    sessionStorage.setItem("doc", "Passport");
                    sessionStorage.setItem("docNo", "2");
                  }}
                >
                  <div className="rounded-5 customShadow  overflow-hidden">
                    <img
                      src={passPort}
                      alt="passPort"
                      className="w-100 imgDoc"
                    />
                    <div className="cardCapition p-3 text-center bg-white">
                      <h3>{t("Passport")}</h3>
                    </div>
                  </div>
                </Link>
              </div>
            ) : (
              ""
            )}
            {settings.oldDrive ? (
              <div className="col-lg-5 col-md-6 p-2">
                <Link
                  to="/insertCard"
                  onClick={(e) => {
                    sessionStorage.setItem("doc", "Old Driving Licence");
                    sessionStorage.setItem("docNo", "4");
                  }}
                >
                  <div className="rounded-5 shadow  overflow-hidden">
                    <img
                      src={oldDriven}
                      alt="oldDriven"
                      className="w-100 imgDoc"
                    />
                    <div className="cardCapition p-3 text-center bg-white">
                      <h3>{t("Old Driving Licence")}</h3>
                    </div>
                  </div>
                </Link>
              </div>
            ) : (
              ""
            )}
          </div>
        </div>
      ) : (
        ""
      )}
    </div>
  );
}
