// Import React, useSelector from react-redux for state management, and Navigate from React Router
import React from "react";
import { useSelector } from "react-redux";
import { Navigate } from "react-router-dom";

// Define a functional component named CheckWelcomeVisit that takes 'props' as a parameter
export default function CheckWelcomeVisit(props) {
  // Use useSelector to access 'welcomeCheck' and 'settings' from the Redux store

  let settings = useSelector((data) => data.settings);

  // Check if 'data.count' is greater than 0
  if (localStorage.getItem("token")) {
    // If 'data.count' is greater than 0, navigate to the "/visitTime" route if 'settings.visitTime' is true,
    // otherwise navigate to the "/companies" route
    return <Navigate to={settings.visitTime ? "/visitTime" : "/companies"} />;
  } else {
    // If 'data.count' is not greater than 0, render the child components passed as 'props'
    return props.children;
  }
}
