import React from "react";
import { useTranslation } from "react-i18next";
import { Link, useNavigate } from "react-router-dom";
import logo from "../../assets/images/avms logo02-01.png";
import { useFormik } from "formik";
import * as yup from "yup";
import axiosInstance from "../../Intercpetor/AddTokenTohttp";
import { useSelector } from "react-redux";
import { useState } from "react";
export default function Login() {
  let [loading, setLoading] = useState(false);
  let [errorMsg, seterrMsg] = useState("");
  let host = useSelector((data) => data.portLoc);
  let settings = useSelector((data) => data.settings);
  const { t, i18n } = useTranslation();
  let Navigatee = useNavigate();
  function toggleFullScreen() {
    if (
      !document.fullscreenElement &&
      !document.mozFullScreenElement &&
      !document.webkitFullscreenElement &&
      !document.msFullscreenElement
    ) {
      // current working methods
      if (document.documentElement.requestFullscreen) {
        document.documentElement.requestFullscreen();
      } else if (document.documentElement.msRequestFullscreen) {
        document.documentElement.msRequestFullscreen();
      } else if (document.documentElement.mozRequestFullScreen) {
        document.documentElement.mozRequestFullScreen();
      } else if (document.documentElement.webkitRequestFullscreen) {
        document.documentElement.webkitRequestFullscreen(
          Element.ALLOW_KEYBOARD_INPUT
        );
      }
    }
  }
  let valdition = yup.object({
    email: yup.string().email().required("Email is required"),
    password: yup
      .string()
      .required("Password is required")
      .min(6, "Password is too short - should be 6 chars minimum"),
  });
  let formik = useFormik({
    initialValues: {
      email: "",
      password: "",
    },
    onSubmit: (x) => {
      sendDatalognin(x);
    },
    validationSchema: valdition,
  });
  async function sendDatalognin(values) {
    setLoading(true);
    let data = await axiosInstance
      .post(`${host.port}api/Auth/getToken`, values)
      .then((res) => {
        localStorage.setItem("token", res.data.token);
        seterrMsg("");
        settings.visitTime ? Navigatee("/visitTime") : Navigatee("/companies");
      })
      .catch((errorrr) => {
        seterrMsg(errorrr.response.data);
        setLoading(false);
      });
    setLoading(false);
  }
  return (
    <>
      {/* opretor Role */}
      <div className="p-3 welcomeLogo">
        <img src={logo} alt="logo" className="w-100" />
      </div>
      <div className="p-5 w-100">
        <h2 className="text-start mb-5">{t("login")}</h2>
        <form onSubmit={formik.handleSubmit}>
          <div className="w-100">
            <label htmlFor="email">{t("email")} :</label>
            <input
              onChange={formik.handleChange}
              type="email"
              className="form-control my-2 w-100"
              name="email"
              id="email"
            ></input>
            <p className="text-danger">{formik.errors.email}</p>
          </div>
          <div className="w-100">
            <label htmlFor="password">{t("password")} :</label>
            <input
              onChange={formik.handleChange}
              type="password"
              className="form-control my-2 w-100"
              name="password"
              id="password"
            ></input>
            <p className="text-danger">{formik.errors.password}</p>
          </div>
          {errorMsg ? (
            <div className="alert alert-danger w-100">{errorMsg}</div>
          ) : (
            ""
          )}
          {loading ? (
            <button type="button" className="btn btn-info">
              <i className="fa-solid fa-spinner fa-spin text-white"></i>
            </button>
          ) : (
            <button
              disabled={!formik.isValid}
              onClick={() => {
                toggleFullScreen();
              }}
              type="submit"
              className="btn btn-info text-white"
            >
              Login
            </button>
          )}
        </form>
      </div>
    </>
  );
}
